.container {
  text-align: center;
  margin-top: 20px;
}

.file-input {
  margin: 20px 0;
}

.image-container {
  width: 100%; /* Set to 100% to fill the available width */
  max-width: 500px; /* Set a maximum width */
  height: auto; /* Allow height to adjust based on width */
  overflow: hidden; /* Hide overflow */
  border: 2px solid #000; /* Optional border for visual effect */
  border-radius: 10px; /* Optional rounded corners */
  margin: 0 auto; /* Center the container */
}

.selected-image {
  width: 100%; /* Make the image take the full width */
  height: auto; /* Maintain aspect ratio */
  object-fit: contain; /* Ensure the entire image is visible within the container */
}

.recognized-text {
  width: 100%; /* Make the textarea responsive */
  max-width: 500px; /* Set a maximum width for the textarea */
  height: 100px; /* Adjust height as needed */
  margin-top: 20px;
}
